export default {
	props: {
		dataSource: {
			type: Object,
			default: () => ({
				ig: '',
				color: '',
				title: '',
				sign: false
			})
		}
	},
	methods: {
		cancleSignHandle() {
			this.$emit('cancleSignHandle', this.dataSource)
		},
		signHandle() {
			this.$emit('signHandle', this.dataSource)
		},
		detailHandle() {
			this.$emit('detailHandle', this.dataSource)
		},
		workHandle() {
			this.$emit('workHandle', this.dataSource)
		}
	}
}
